* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  margin: 0; 
  height: 100%; 
}

body {
  background: #000000;
  margin: 0;
  padding: 0;
}

.section-1 {
  background: rgb(0,0,0);
  background: linear-gradient(200deg, rgba(0,0,0,1) 8%, rgba(134,0,255,1) 60%, rgba(255,0,251,1) 100%);
    background: url(https://i.imgur.com/QjiKsMr.jpg)
      center no-repeat;
    background-size:contain;

}

.nameHeader {
  text-align: left;
  color: #00aeff;
  margin-left: 4%;
  font-size: 8rem;
  -webkit-user-select: none;
  -moz-user-select: none; 
  -ms-user-select: none; 
  user-select: none;
}

nav {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 5%;
  gap: 1%;
  width: 100%;
}

.button {
  margin-top: 1%;
  border: 1px solid transparent;
  border-radius: .75rem;
  background-color: #000000;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  flex: 0 0 auto;
  font-family: "Inter var",ui-sans-serif,system-ui,-apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1.125rem;
  font-weight: 600;
  line-height: 1.5rem;
  text-align: center;
  text-decoration: none #000000 solid;
  text-decoration-thickness: auto;
  transition-duration: .2s;
  transition-property: background-color,border-color,color,fill,stroke;
  transition-timing-function: cubic-bezier(.4, 0, 0.2, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: auto;
}

.button:hover {
  background-color: #374151;
}

.button:focus {
  box-shadow: none;
  outline: 2px solid transparent;
  outline-offset: 2px;
}

@media (min-width: 768px) {
  .button {
    padding: .75rem 1.5rem;
  }
}

.gradient {
  background: rgb(252,70,107);
  background: radial-gradient(circle, rgba(252,70,107,1) 0%, rgba(63,185,251,1) 100%);
  height: 0.2rem;
}

.spacer {
  height: 8rem;
}

.section-2 {
  background: rgb(162,162,162);
  background: radial-gradient(circle, rgb(0, 0, 0) 0%, rgb(0, 0, 0) 100%);
}

.howdy {
  text-align: left;
  margin-left: 4%;
  font-size: 3rem;
  color: #ffffff;
  width: 40%;
}

.para {
  margin-top: 5rem;
  font-size: 2rem;
}

.projects {
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: left;
  margin-left: 4%;
  color: #ffffff;
}

.code {
  color: #ff01ea;
  opacity: 0.5;
  animation: text-flicker 2s linear infinite;
}

@keyframes text-flicker {
  0% {
    opacity: 0.1;
  }

  2% {
    opacity: 1;
  }

  8% {
    opacity: 0.1;
    color:#ffffff;
  }

  9% {
    opacity: 1;
  }

  12% {
    opacity: 0.1;
  }
  20% {
    opacity: 1;
  }
  25% {
    opacity: 0.3;
  }
  30% {
    opacity: 1;
  }

  70% {
    opacity: 0.7;
  }
  72% {
    opacity: 0.2;
  }

  77% {
    opacity: 0.9;
  }
  100% {
    opacity: 0.9;
  }
}

.card {
    margin-left: 4%;
    margin-bottom: 8%;
    width:350px;
    height: 44rem;
    box-shadow:0 15px 35px rgba(0,0,0,0.5);
    border-radius:10px;
    background: rgba(255,255,255,0.05);
    backdrop-filter:blur(10px);

    
    .card__top{
           position:relative;
      img{
        border-radius: 5%;
        width:100%;
       height:100%;
        object-fit:cover;
      }
      
         .profile__photo{
    position: absolute;
    bottom: -170px;
left:50%;
           transform:translatex(-50%);
   
      img{
        width:100%;
        border-radius:10%;
        height:100%;
        object-fit:cover;
           border:7px solid rgba(0,0,0,0.35);

      }
    }
    }
    .card__content{
      text-align:center;
      color:#fff;
      padding:5em 2.5em;
      h2{
        font-weight:700;
          font-size:24px;
          margin-top: 35%;
      }
      
      p{
        margin-top:2%;
        text-align:center;
        margin-bottom: 5%;

      }
      p+p{
        margin-top:10px;
      }
      
    }
  }

  .hero {
    font-size: clamp(40px, 10vw, 100px);
    line-height: 1;
    display: inline-block;
    color: #fff;
    z-index: 2;
    letter-spacing: 10px;
  
    /* Bright things in dark environments usually cast that light, giving off a glow */
    filter: drop-shadow(0 1px 3px);
  }

  .layers {
    position: relative;
  }
  
  .layers::before,
  .layers::after {
    content: attr(data-text);
    position: absolute;
    width: 110%;
    z-index: -1;
  }
  
  .layers::before {
    top: 10px;
    left: 15px;
    color: #e0287d;
  }
  
  .layers::after {
    top: 5px;
    left: -10px;
    color: #1bc7fb;
  }
  
